.main-content {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background: linear-gradient(rgb(74, 109, 76), black);
    position: relative;
}

.intro {
    max-width: 75%;
    overflow-wrap: break-word;
    padding: 1rem;
    position: absolute;
    animation: slide-in 1s forwards, fade-in 2s;
}

@keyframes slide-in {
    from {
        transform: translateX(-100%);
    }
    to {
        transform: translateX(0);
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.intro h1 {
    text-shadow: 2px 2px 8px rgb(12, 11, 11);
    letter-spacing: 3px;
    color: white;
}

@media (max-width: 500px) {
    .main-content {
        font-size: .7rem;
    }

    .intro {
        max-width: 80%;
        text-align: center;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
} 